import React from 'react'
import Grid from '../Components/Grids/Grid'
import CategoryList from '../Components/Categories/CategoryList';
import { connect } from 'react-redux'
import { setCategory, setCatContent } from '../Actions/content';
import Axios from 'axios';

class Category extends React.Component{
  state = {
    content: false,
    category: false,
    filter: '3'
  }

  componentDidMount() {
    Axios.get(`/api/content/category`).then((res) => {
      this.props.setCategory(res.data);
      this.setState({
        category: true,
        filter: res.data[0]
      })
      this.filterComponent();
    }).catch((e) => {
      this.setState({
        category: false
      })
      console.log('error found in ./api/content/', e);
    })

    console.log(this.state.filter)
    Axios.get(`/api/content/category/${this.state.filter}`).then((res) => {
      this.props.setCatContent(res.data);
      console.log(res.data)
      this.setState({
        content: true
      })
    }).catch((e) => {
      this.setState({
        content: false
      })
      console.log('error found in ./api/content/', e);
    })
  }

  filterCategory = (filter) => {
    this.setState({
      filter
    })

  }

  render(){
    const props = this.props;
    return (
      <div className="content">
      <div className="categories">
        {this.state.category ? 
          <CategoryList category={this.props.category} filter={this.filterCategory}/>
        : 'Loading'}
        <div className="categories-types">
          <div className="recent-videos">
            <h5><img src="/images/recent.png" alt="" />Recent-videos</h5>
            <div className="grids grids2">
            {this.state.content ? 
              props.cat_content.map((content, key) => {
                return <Grid content={content} key={content._id}/>
              })
            : 'Loading'}
            </div>

          </div>
          <div className="recent-videos">
            <h5><img src="/images/recent.png" alt="" />Most-View-videos</h5>
            {/* <div className="grids">
              <Grid/>
              <Grid/>
              <Grid/>
            </div> */}
          </div>
        </div>
      </div>
      <div className="clear"> </div>
      </div>
    )
  }
}
const mapStateToProps = (state) => ({
  allContent: state.content.all,
  category: state.content.category,
  cat_content: state.content.cat_content
});

const mapDispatchToProps = (dispatch) => ({
  setCategory: (data) => dispatch(setCategory(data)),
  setCatContent: (data) => dispatch(setCatContent(data))
})

export default connect(mapStateToProps, mapDispatchToProps)(Category)
